import {display_name} from "./time"
import TestSet, {QuestionData, Score, dateString} from "./TestSet";


const getItem = window.localStorage.getItem.bind(window.localStorage);
const setItem = window.localStorage.setItem.bind(window.localStorage);

type Meta = {
  version: number,
  tests: Array<{id: string, description: string}>
};

export default class Store {
  private static version: number = 51;
  private meta: Meta;
  private current_test_set: TestSet;

  constructor() {
    const meta_json = getItem("meta");
    this.meta = meta_json ? JSON.parse(meta_json) : {
      version: 0,
      tests: new Array<{id: string, description: string}>()
    };
    if (this.meta.version != Store.version) {
      this.set_meta();
    }
    this.current_test_set = this.new_test_set(this.meta.tests[0].id);
  }

  public get_test_list(): Array<{id: string, description: string}> {
    return this.meta.tests;
  }

  public get_current_test_set(): TestSet {
    return this.current_test_set;
  }

  public set_current_test(test_id: string): void {
    this.current_test_set = this.new_test_set(test_id);
  }

  public get_score(test_id: string): Score | null {
    const score_json = getItem(`${test_id}_${dateString()}`);
    return score_json ? JSON.parse(score_json) : score_json;
  }

  private set_meta() {
    this.meta.version = Store.version;
    this.meta.tests = new Array<{id: string, description: string}>();
    let id = 0;

    // Add number bond test
    for (let sum = 10; sum <= 20; sum += 10, ++id) {
      const id_str = `test_${id}`;
      this.meta.tests.push({ id: id_str, description: `Number bonds up to ${sum}` });
      setItem(`${id}_current`, "0");
      const questionData = new Array<QuestionData>();
      for (let x = 1; x < sum; ++x) {
        for (let y = 1; x + y <= sum; ++y) {
          questionData.push({
            text: `${x} + ${y} = `,
            answer: x + y,
            level: 10,
            display_component: "Text",
            input_component: "MultipleChoice"
          });
        }
      }
      setItem(id_str, JSON.stringify(questionData));
    }

    // Add subtraction tests
    for (let x = 10; x <= 20; x += 10, ++id) {
      const id_str = `test_${id}`;
      this.meta.tests.push({ id: id_str, description: `Subtraction up to ${x}` });
      setItem(`${id}_current`, "0");
      const questionData = new Array<QuestionData>();
      for (let y = 1; y < x; ++y) {
        questionData.push({
          text: `${x} - ${y} = `,
          answer: x - y,
          level: 10,
          display_component: "Text",
          input_component: "MultipleChoice"
        });
      }
      setItem(id_str, JSON.stringify(questionData));
    }

    // Add colour string notes
    {
      const id_str = `test_${id}`;
      this.meta.tests.push({ id: id_str, description: "Colourstring - notes" });
      setItem(`${id}_current`, "0");
      const questionData = new Array<QuestionData>();
      for (let note_index = -2; note_index <= 13; note_index++) {
        questionData.push({
          text: note_index.toString(),
          answer: this.get_colourstring_note(note_index),
          level: 10,
          display_component: "Stave",
          input_component: "MultipleChoice"
        });
      }
      setItem(id_str, JSON.stringify(questionData));
    }

    // Add time 1 test
    id++;
    {
      const id_str = `test_${id}`;
      this.meta.tests.push({ id: id_str, description: "Time 1" });
      setItem(`${id}_current`, "0");
      const questionData = new Array<QuestionData>();
      for (let h = 1; h <= 12; h++) {
        for (let m = 0; m <= 55; m+=5) {
          questionData.push({
            text: [h, m],
            answer: display_name(h, m),
            level: 10,
            display_component: "Clock",
            input_component: "MultipleChoice"
          });
        }
      }
      setItem(id_str, JSON.stringify(questionData));
    }

    setItem("meta", JSON.stringify(this.meta));
  }

  private new_test_set(test_id: string): TestSet {
    const testSet_json = getItem(test_id);
    const questions = testSet_json ? JSON.parse(testSet_json) : [
      {
        text: "0 + 0",
        answer: 0,
        level: 1
      }
    ];

    const current_question_json = getItem(`${test_id}_current`);
    const current_question = current_question_json ? Number(current_question_json) : 0;

    const score_json = getItem(`${test_id}_${dateString()}`);
    const score = score_json ? JSON.parse(score_json) : {correct:0, wrong:0};

    return new TestSet(test_id, questions, current_question, score);
  }

  private get_colourstring_note(note_index: number): string {
    const string_names = ["Birdie", "Mummy", "Daddy", "Bear"];
    const sol_fa_numes = ["Do", "Re", "Mi", "Fa"];
    const string_index = Math.floor((note_index + 2)/4);
    const sol_fa_index = (13 - note_index) % 4;
    const note = String.fromCharCode(((13 - note_index + 6) % 7) + 65);
    return `${string_names[string_index]} ${sol_fa_numes[sol_fa_index]} (${note})`;
  }
}
