import _ from "lodash";

import moment from "moment";


export class Question {
  constructor(public readonly text: any,
              public readonly index: number,
              public readonly display_component: string,
              public readonly input_component: string,
              public readonly isCorrect: (answer: number | string) => boolean) {
  }
}

export type Score = {
  correct: number,
  wrong: number
}

export type QuestionData = {
  text: any,
  answer: number | string,
  level: number,
  display_component: string,
  input_component: string
}

export const dateString = (): string => {
  return moment().format('YYYYMMDD');
}

export default class TestSet {
  private test_id: string;
  private questions: Array<QuestionData>;
  private score: Score;
  private first_question: number;
  private is_first_question: boolean;
  private dateKey: String;

  constructor(test_id: string, questions: Array<QuestionData>, first_question: number, score: Score) {
    this.test_id = test_id;
    this.questions = questions;
    this.first_question = first_question;
    this.is_first_question = true;
    this.score = score;
    this.dateKey = dateString();
  }

  public getFirstQuestion(): Question {
    return this.getQuestion(this.first_question);
  }

  public getRandomQuestion(): Question {
    const index = this.getRandomIndex();
    window.localStorage.setItem(`${this.test_id}_current`, String(index));
    return this.getQuestion(index);
  }

  public getMultipleChoiceQuestions(question: Question,
    number_of_answers: number): Array<number | string> {
    const answers = new Array<number | string>();
    answers.push(this.questions[question.index].answer);
    while (answers.length < number_of_answers) {
      const i = _.random(0, this.getNumberOfQuestions() - 1);
      const answer = this.questions[i].answer;
      if (_.find(answers, a => answer == a) == undefined) {
        answers.push(answer);
      }
    }
    if (typeof answers[0]  === 'number') {
      return answers.sort((a, b) => +a - +b);
    } else {
      return answers.sort();
    }
  }

  public getScore(): Score {
    return this.score;
  }

  public getNumberOfQuestions(): number {
    return this.questions.length;
  }

  private getQuestion(index: number): Question {
    const question = this.questions[index];
    return new Question(question.text, index, question.display_component,
      question.input_component, (answer: number | string) => {
      return this.answerQuestion(question, answer);
    });
  }

  private getRandomIndex(): number {
    const r = _.random(0, this.getLevelSum() - 1);
    let index = 0;
    let cumSum = this.questions[index].level;
    while (r >= cumSum) {
      index++;
      cumSum += this.questions[index].level;
    }
    return index;
  }

  private answerQuestion(question: QuestionData, answer: number | string): boolean {
    if (dateString() != this.dateKey) {
      this.dateKey = dateString();
      this.score.correct = 0;
      this.score.wrong = 0;
    }
    const isCorrect: boolean = question.answer == answer;
    if (isCorrect) {
      this.score.correct++;
      if (question.level > 1) {
        question.level--
      }
    } else {
      this.score.wrong++;
      question.level = 10;
    }
    window.localStorage.setItem(`${this.test_id}`, JSON.stringify(this.questions));
    window.localStorage.setItem(`${this.test_id}_${this.dateKey}`, JSON.stringify(this.score));
    return isCorrect;
  }

  private getLevelSum(): number {
    return this.questions.reduce((sum, question) => sum + question.level, 0);
  }
}
