import * as React from "react";

import Store from "../Store";
import {Score} from "../TestSet";
import {randomHappyFace, randomSadFace} from "../randomFaces"


interface Props {
  store: Store,
  onClick: (test_id: string) => void
}

const TestList: React.FC<Props> = (props: Props) => {
  const total_score: Score = {correct: 0, wrong: 0};
  const list_items = props.store.get_test_list().map(test => {
    const score = props.store.get_score(test.id);
    if (score) {
      total_score.correct += score.correct;
      total_score.wrong += score.wrong;
    }
    return (
      <li key={test.id} className={`btn list-group-item d-flex justify-content-between
      align-items-center ${score && score.correct ? "list-group-item-success" : ""}`}
          onClick={(e: any) => { props.onClick(test.id); }}>
        <h5>{test.description}</h5>
        {
          score ?
            <div>
              <h5>{randomHappyFace()} x {score.correct} </h5>
              <h5>{randomSadFace()} x {score.wrong}</h5>
            </div> :
            null
        }
      </li>
    )
  });

  return (
    <div className='container mt-3'>
      <ul className="list-group">
        <li className="list-group-item bg-light flashing-text">Hello Mia! You're amazing!! love daddy xxx</li>
        {list_items}
        {
          total_score.correct || total_score.wrong ?
          <li className="btn list-group-item d-flex justify-content-between
          align-items-center list-group-item-primary mt-2">
            <h4>Total</h4>
            <div>
              <h4>{randomHappyFace()} x {total_score.correct} </h4>
              <h4>{randomSadFace()} x {total_score.wrong}</h4>
            </div>
          </li> :
          null
        }
      </ul>
    </div>
  );
}

export default TestList;
