import _ from "lodash";
import * as React from "react";
import {ArrowReturnLeft} from "react-bootstrap-icons";

import TestSet, {Question, Score} from "../TestSet";
import {randomHappyFace, randomSadFace} from "../randomFaces"

import ClockDisplay from "./ClockDisplay";
import MultipleChoice from "./MultipleChoice"
import NumberPad from "./NumberPad";
import Stave from "./Stave"


interface Props {
  testSet: TestSet
  initialQuestion: Question
  initialAnswers: Array<number|string>
  initialHappyFace: string
  initialSadFace: string
  onBack: () => void
}

const Testing: React.FC<Props> = (props: Props) => {
  const [question, setQuestion] = React.useState<Question>(props.initialQuestion);
  const [answerText, setAnswerText] = React.useState<string>("?");
  const [answers, setAnswers] = React.useState<Array<number|string>>(props.initialAnswers);
  const [progress, setProgress] = React.useState(100);
  const [happyFace, setHappyFace] = React.useState<string>(props.initialHappyFace);
  const [sadFace, setSadFace] = React.useState<string>(props.initialSadFace);
  const intervalRef = React.useRef<NodeJS.Timeout | null>(null);

  React.useEffect(() => {
    setProgress(100);
    intervalRef.current = setInterval(() => {
      setProgress(prevProgress => {
        if (prevProgress <= 0) {
          clearInterval(intervalRef.current as NodeJS.Timeout);
          return 0;
        }
        return prevProgress - 2;
      });
    }, 100);

    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
    };
  }, [question]);

  const onAnswer = (answer: number | string) => {
    if (question.isCorrect(answer)) {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
      const hf = randomHappyFace();
      setHappyFace(hf);
      setAnswerText(`${answer} ${hf}`);
      setTimeout(onCorrectAnswer, 1000);
    } else {
      const sf = randomSadFace();
      setSadFace(sf);
      setAnswerText(`? ${sf}`);
      setTimeout(onIncorrectAnswer, 1000);
    }
  };

  const onCorrectAnswer = () => {
    // Select a new random questions that's not the same as the previous
    // question
    let newQuestion = question;
    while (newQuestion.index == question.index &&
           props.testSet.getNumberOfQuestions() > 1) {
      newQuestion = props.testSet.getRandomQuestion();
    }
    setQuestion(newQuestion);
    setAnswerText("?");
    setAnswers(props.testSet.getMultipleChoiceQuestions(newQuestion, 6));
  };

  const onIncorrectAnswer = () => {
    setAnswerText("?");
  };

  const score: Score = props.testSet.getScore();
  return (
    <div className="container-fluid">
      <div className="row justify-content-center">
        <div className="col-sm-11 mt-3">
          <div className={`rounded ${progress == 0? "bg-success": "bg-primary"} bg-gradient`}>
          <div className="progress" style={{height: "5px", marginBottom: "5px"}}>
            <div className="progress-bar bg-warning" role="progressbar"
              style={{ width: `${progress}%`,
                       transition: progress === 100 ? 'width 0.01s ease-in-out' : 'width 0.1s linear'
                     }}
              aria-valuenow={progress} aria-valuemin={0} aria-valuemax={100}>
            </div>
          </div>
            {question.display_component === 'Text' ? (
              <h1 className="display-3 text-white text-center noselect">{question.text} {answerText}</h1>
            ) : question.display_component === 'Stave' ? (
              <div>
                <Stave note_index={parseInt(question.text)}/>
                <h2 className="pb-2 display-2 text-white text-center noselect">{answerText}</h2>
              </div>
            ) : question.display_component === 'Clock' ? (
              <div>
                <ClockDisplay time={question.text}/>
                <h2 className="pb-2 display-2 text-white text-center noselect">{answerText}</h2>
              </div>
            ) : null}
          </div>
          {question.input_component === 'MultipleChoice' ? (
            <MultipleChoice answers={answers} onClick={onAnswer}/>
          ) : question.input_component === 'NumberPad' ? (
            <NumberPad answer={null} onClick={onAnswer}/>
          ) : null}
          <div className="pt-3">
            <h3 className="noselect"> {happyFace} x {score.correct}</h3>
            <h3 className="noselect">{sadFace} x {score.wrong}</h3>
            <ArrowReturnLeft onClick={(e: any) => { props.onBack(); }}/>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Testing;
