export const display_name = (h: number, m: number): string => {
  if (m === 1) {
    return `${m} min past ${h}`;
  } else
  if (m === 59) {
    return `${m} min to ${h == 12 ? 1 : h + 1}`;
  } else
  if (m === 0 ) {
    return `${h} o'clock`;
  } else
  if (m === 15) {
    return `quarter past ${h}`;
  } else
  if (m === 30) {
    return `half past ${h}`;
  } else
  if (m === 45) {
    return `quarter to ${h == 12 ? 1 : h + 1}`;
  } else
  if (m < 30) {
    return `${m} mins past ${h}`;
  } else
  if (m > 30) {
    return `${60 - m} mins to ${h == 12 ? 1 : h + 1}`;
  }
  return "";
};
