import * as React from "react";

import {randomHappyFace, randomSadFace} from "../randomFaces"
import Store from "../Store";
import TestSet from "../TestSet";
import Testing from "./Testing";
import TestList from "./TestList";


interface Props {
  store: Store
}

const App: React.FC<Props> = (props: Props) => {
  const [testSet, setTestSet] = React.useState<TestSet|null>(null);
  const [isTesting, setIsTesting ] = React.useState<boolean>(false);

  const test_selected = (test_id: string) => {
    props.store.set_current_test(test_id);
    setTestSet(props.store.get_current_test_set());
    setIsTesting(true);
  };

  const stop_testing = () => {
    setIsTesting(false);
  }

  return (
    <div>
      {
        isTesting &&
        testSet !== null ?
        <Testing testSet={testSet}
                 initialQuestion={testSet.getFirstQuestion()}
                 initialAnswers={testSet.getMultipleChoiceQuestions(testSet.getFirstQuestion(), 6)}
                 initialHappyFace={randomHappyFace()}
                 initialSadFace={randomSadFace()}
                 onBack={stop_testing}></Testing> :
        <TestList store={props.store} onClick={test_selected}/>
      }
    </div>
  );
}

export default App;
