import React from 'react';

interface Props {
  note_index: number;
}

const Stave: React.FC<Props> = (props: Props) => {
  // Dimensions and positions for the stave and note
  const stave_width = 500;
  const stave_line_spacing = 30;
  const stave_extra_space_top = 60; // Extra space for high notes
  const stave_extra_space_bottom = 90; // Extra space for low notes
  const note_x_radius = 16;
  const note_y_radius = 12;
  const note_x_position = 250; // Horizontal position of the note
  const rotation_angle = -25; // Angle for anticlockwise rotation

  const open_strings_note_x_position = 150;
  const open_strings = [1, 5, 9, 13];
  const string_colours = ["yellow", "blue", "red", "green"];

  // Calculate the Y positions of the stave lines, adding extra space at the top
  const stave_lines_y = Array.from({ length: 5 }, (_, i) => i * stave_line_spacing + stave_extra_space_top);

  // Calculate the center of the ellipse for rotation
  const note_center_y = (note_index: number): number => {
    return stave_extra_space_top + note_index * stave_line_spacing / 2;
  };

  // Determine if a ledger line is needed
  const extra_lines = new Array<number>();
  for (let i = 10; i <= props.note_index; i += 2) {
    extra_lines.push(stave_extra_space_top + i * stave_line_spacing / 2)
  }
  for (let i = -2; i >= props.note_index; i -= 2) {
    extra_lines.push(stave_extra_space_top + i * stave_line_spacing / 2)
  }

  return (
    <div className="pt-3 d-flex justify-content-center">
      <svg width={stave_width}
           height={stave_line_spacing * 4 + stave_extra_space_top + stave_extra_space_bottom}
           style={{ backgroundColor: 'white' }}>
        <image
          href="treble_clef.png"
          x={-5}
          y={-5}
          height={250}
        />
        {stave_lines_y.map(y => (
          <line key={y} x1="0" y1={y} x2={stave_width} y2={y}
                stroke="black" strokeWidth="3" />
        ))}
        {extra_lines.map(y => (
          <line x1={note_x_position - note_x_radius * 1.5}
                y1={y}
                x2={note_x_position + note_x_radius * 1.5}
                y2={y}
                stroke="black"
                strokeWidth="3" />
        ))}
        <ellipse
          cx={note_x_position}
          cy={note_center_y(props.note_index)}
          rx={note_x_radius}
          ry={note_y_radius}
          fill="black"
          transform={`rotate(${rotation_angle} ${note_x_position} ${note_center_y(props.note_index)})`}
        />
        {Array.from({ length: 4 }, (_, i) => (
        <ellipse
          cx={open_strings_note_x_position}
          cy={note_center_y(open_strings[i])}
          rx={note_x_radius}
          ry={note_y_radius}
          fill={string_colours[i]}
          opacity="0.5"
          transform={`rotate(${rotation_angle} ${open_strings_note_x_position} ${note_center_y(open_strings[i])})`}
        />
        ))}
      </svg>
    </div>
  );
};

export default Stave;
