import * as React from "react";
import {Backspace, ArrowReturnLeft} from "react-bootstrap-icons";


interface Props {
  answer: number | null,
  onClick: (answer: number | string) => void
}

const NumberPad: React.FC<Props> = (props: Props) => {
  const [answer, setAnswer ] = React.useState<number | null>(props.answer);

  const onDigit = (digit: number) => {
    if (answer === null) {
      setAnswer(digit);
    } else if (answer < 1000) {
      setAnswer(digit + 10*answer);
    }
  };

  const onBack = (e: any) => {
    if (answer !== null) {
      if (answer < 10) {
        setAnswer(null);
      } else {
        setAnswer(Math.floor(answer/10));
      }
    }
  };

  const onAnswer = (e: any) => {
    if (answer !== null) {
      props.onClick(answer);
      setAnswer(null);
    }
  };

  return (
    <div className="d-flex justify-content-center">
      <table>
        <tbody>
          <tr>
            <td colSpan={3}
                className="h2 align-middle text-right border border-1 pr-2 bg-light bg-gradient noselect">
              {answer == null ? <div>&nbsp;</div> : answer}
            </td>
          </tr>
          <tr>
            <Digit digit={1} onClick={onDigit}/>
            <Digit digit={2} onClick={onDigit}/>
            <Digit digit={3} onClick={onDigit}/>
          </tr>
          <tr>
            <Digit digit={4} onClick={onDigit}/>
            <Digit digit={5} onClick={onDigit}/>
            <Digit digit={6} onClick={onDigit}/>
          </tr>
          <tr>
            <Digit digit={7} onClick={onDigit}/>
            <Digit digit={8} onClick={onDigit}/>
            <Digit digit={9} onClick={onDigit}/>
          </tr>
          <tr>
            <td className="align-middle text-center h3 bg-secondary bg-gradient"
                onClick={onBack} >
              <Backspace/>
            </td>
            <Digit digit={0} onClick={onDigit}/>
            <td className="align-middle text-center h3 bg-secondary bg-gradient"
                onClick={onAnswer}>
              <ArrowReturnLeft/>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  );
}

interface DigitProps {
  digit: number,
  onClick: (answer: number) => void
}

const Digit: React.FC<DigitProps> = (props: DigitProps) => {
  const [background, setBackground] = React.useState<string>("");
  return (
    <td className={`h1 align-middle text-center border border-1 pt-2 pb-2 pl-4 pr-4 noselect ${background}`}
        onClick={(e: any) => {
          props.onClick(props.digit);
          e.target.blur();
          setBackground("bg-success bg-gradient");
          setTimeout(() => { setBackground(""); }, 200);
        }}>
      {props.digit}
    </td>
  );
}

export default NumberPad;
